import React, { useContext, useEffect, useState } from "react";
import axios from "axios";
import { useFormik } from "formik";
import * as yup from "yup";
import {
  CategoryContext,
  SnackContext,
  UserContext,
  UserProContext,
} from "../context/context";
import { passwordRegExp, phoneRegExp } from "../Utils/RegularExpressions";

export const useAddClient = () => {
  const [open_add, setOpen_add] = useState(false);
  const { setSnack } = useContext(SnackContext);
  const { category } = useContext(CategoryContext);
  const { userProfile } = useContext(UserProContext);
  const { user, setUser } = useContext(UserContext);
  const [management, setManagement] = useState([]);
  const [ccteam, setCcteam] = useState([]);
  const [finance, setFinance] = useState([]);
  const password = passwordRegExp;
  const phoneNumber = phoneRegExp;

  const getAvailability = () => {
    axios.post("/api/user_select1").then(function (res) {
      if (res.data.status === true) {
        console.log(res.data.data);
        var id = userProfile.user_id;
        console.log(id);
        res.data.data.map((data) => {
          if (
            data.user_cat_id === 401 &&
            data.availability === 1 &&
            data.user_status === 1&&
            data.user_added_by == userProfile.user_id
          ) {
            var cc_team = { id: data.user_id, name: data.user_name };
            console.log(cc_team);
            ccteam.push(cc_team);
          }
          console.log(ccteam);
        });
        res.data.data.map((data) => {
          if (
            data.user_cat_id === 701 &&
            data.availability === 1 &&
            data.user_status === 1&&
            data.user_added_by == userProfile.user_id
          ) {
            var mngmt = { id: data.user_id, name: data.user_name };
            management.push(mngmt);
          }
          console.log(management);
        });
        res.data.data.map((data) => {
          if (
            data.user_cat_id === 601 &&
            data.availability === 1 &&
            data.user_status === 1&&
            data.user_added_by == userProfile.user_id
          ) {
            var finan = { id: data.user_id, name: data.user_name };
            finance.push(finan);
          }
          console.log(finance);
        });
      }
    });
  };
  const getNormal = () => {
    setCcteam([]);
    setManagement([]);
    setFinance([]);
  };
  const formikAdd = useFormik({
    initialValues: {
      management: { id: "", name: "" },
      finance: { id: "", name: "" },
      ccteam: { id: "", name: "" },
      email: "",
      mobile: "",
      pwd: "",
      name: "",
      doctor:"",
      nurse:"",
      compounder:""
    },
    validationSchema: yup.object({
      management: yup.object({
        id: yup.string().required("Select Management"),
        name: yup.string().required("Select Management"),
      }),
      finance: yup.object({
        id: yup.string().required("Select Finance"),
        name: yup.string().required("Select Finance"),
      }),
      ccteam: yup.object({
        id: yup.string().required("Select Command Center Team"),
        name: yup.string().required("Select Command Center Team"),
      }),
      name: yup
        .string()
        .min(2, "Too Short!")
        .max(50, "Too Long!")
        .required("Name is Required"),
      email: yup
        .string()
        .email("Enter a valid email")
        .required("Email is required"),
      mobile: yup
        .string()
        .matches(phoneNumber, "Mobilenumber is not valid")
        .min(10, "Enter a valid Mobilenumber of 10 numbers")
        .max(10, "Mobilenumber is not valid")
        .required("Mobilenumber is required"),
      pwd: yup
        .string()
        .matches(password, "Password is invalid")
        .min(6, "Password is too short")
        .required("Enter Your Password"),
    }),
    onSubmit: async (values, { resetForm }) => {
      console.log(values);
      getNormal();

      try {
        const formdata = new FormData();
        formdata.append("uname", values.name);
        formdata.append("umail", values.email);
        formdata.append("umobile", values.mobile);
        formdata.append("upass", values.pwd);
        formdata.append("ucat", category.name);
        formdata.append("ucatid", category.id);
        formdata.append("cstid", userProfile.user_id);
        formdata.append("cctid", values.ccteam.id);
        formdata.append("cctname", values.ccteam.name);
        formdata.append("doctor", values.doctor);
        formdata.append("nurse", values.nurse);
        formdata.append("compounder", values.compounder);
        formdata.append("managementid", values.management.id);
        formdata.append("managementname", values.management.name);
        formdata.append("financeid", values.finance.id);
        formdata.append("financename", values.finance.name);
        axios.post("/api/adding_client", formdata).then(function (res) {
          if (res.data.status === true) {
            console.log(res.data.data.client_id);
            const formdata1=new FormData()
            formdata1.append("clinic_id", res.data.data.client_id)
            formdata1.append("nurse", values.nurse) 
            formdata1.append("doctor", values.doctor);
        formdata1.append("compounder", values.compounder);
            formdata1.append("name", values.name);
            formdata1.append("email", values.email);
            formdata1.append("mob", values.mobile);
            formdata1.append("pass", values.pwd);
            formdata1.append("device_id", "testDevice1");
            axios.post("http://44.205.133.187:8001/api/signup", formdata1).then(function (res) {
              if (res.data.status === true) {
                console.log(res.data.data);
                setSnack({
                  message: res.data.msg,
                  type: "success",
                  open: true,
                });
              }else{console.log("failed")}
            
            }).catch(
              (err)=>{console.log(err)}
            )
            setOpen_add(false);
            getdata();
          } else {
            setSnack({
              message: res.data.msg,
              type: "success",
              open: true,
            });
          }
          console.log("=============");
        });
      } catch (err) {
        console.log("Error");
      }
      resetForm({});
    },
  });
  const getdata = () => {
    const formdata = new FormData();
    // formdata.append("ucatid", category.id);
    formdata.append("cstid", userProfile.user_id);
    axios.post("/api/select_new_client", formdata).then(function (res) {
      if (res.data.status === true) {
        setUser(res.data.data);
      }
    });
  };

  return [
    formikAdd,
    open_add,
    setOpen_add,
    management,
    finance,
    ccteam,
    getAvailability,
    getNormal,
  ];
};
