
import react, { useState } from "react";
import { useFormik } from "formik";
import * as yup from "yup";
import { useNavigate } from 'react-router-dom'
import axios from "axios";

export const usePassword = () => {
  const [email, setEmail] = useState();
  const [open, setOpen] = useState(false);
  const navigate = useNavigate();
  const PasswordRegExp = /^(?=.*\d)(?=.*[!@#$%^&*])(?=.*[a-z])(?=.*[A-Z]).{8,}$/;
  
  const formik_mail = useFormik({
    initialValues: {
      email: "",
    },
    validationSchema: yup.object({
      email: yup
        .string()
        .email("Enter a valid email")
        .required("Email is required"),
    }),
    onSubmit: async (state) => {
        const formdata = new FormData();
        formdata.append("uname", state.email);
        axios.post("/api/forgot_password", formdata).then(function (res) {
            if (res.data.status === true) {
                // setLoading(false);
                setOpen(true)
              
            } else {
                console.log("Error")
            }
        });
    }
  });
  const formik = useFormik({
    initialValues: {
      email: formik_mail.values.email,
      pwd: "",
      new_pwd: "",
    },
    validationSchema: yup.object({
      email: yup
        .string()
        .email("Enter a valid email")
        .required("Email is required"),
      pwd: yup
        .string()
        .matches(PasswordRegExp, "Password should contain 1 special character,1 smallcase letter,1 uppercase letter, 1 number,contain 8 characters")
        .required("Pasword should not be empty"),
      new_pwd: yup
        .string()
        .required("confirm your password")
        .oneOf([yup.ref("pwd"), null], "Password doesn't match"),
    }),
    enableReinitialize:true,
    onSubmit: async (state) => {
      console.log(state);
      const formdata = new FormData();
      formdata.append("mail", state.email);
      formdata.append("pas", state.new_pwd);
      await axios.post("/api/update_password", formdata).then(function (res) {
        console.log(res.data);
        if (res.data.status === true) {
          console.log(state.new_pwd);
          setOpen(false);
          navigate("/")
        } else {
          console.log("Error");
        }
      });
    },
  });
  return [formik, open, formik_mail];
};