import React, { useContext } from "react";
import { MenuItem } from "@mui/material";
import CustomGrid from "../Reusable/CustomGrid";
import CustomBox from "../Reusable/CustomBox";
import CustomTable from "../Reusable/CustomTable";
import CustomContainer from "../Reusable/CustomContainer";
import CustomDialog from "../Reusable/CustomDilog";
import CustomTextField from "../Reusable/CustomTextfield";
import CustomTypography from "../Reusable/CustomTypography";
import CustomButton from "../Reusable/CustomButton";
import FormikButton from "../Reusable/FormikButton";
import { IconButton } from "@mui/material";
import EditOutlinedIcon from "@mui/icons-material/EditOutlined";
import DeleteOutlineOutlinedIcon from "@mui/icons-material/DeleteOutlineOutlined";
import DataTable from "react-data-table-component";
import DataTableExtensions from "react-data-table-component-extensions";
import "react-data-table-component-extensions/dist/index.css";
import { useAddUser } from "../User/AddUserFunction";
import { useUserData } from "../User/UserDataFunction";
import { useEditUser } from "../User/EditUserFunction";
import { useDeleteFinance } from "./DeleteFinanceFunction";
import { UserProContext } from "../context/context";
import { clientColumns } from "../Utils/ClientColumns";
import CustomClientTable from "../Client/CustomClientTable";

export const FinanceData = () => {
  const [data, tkn] = useUserData("/api/user_select");
  const [formikAdd, open_add, setOpen_add] = useAddUser("/api/user_add");
  const [editClick, formikEdit, open_edit, setOpen_edit] = useEditUser();
  const [
    open_tag,
    setOpen_tag,
    open_client,
    setOpen_client,
    client,
    finance,
    formikClientEdit,
    editClient,
    deleteClick,
  ] = useDeleteFinance();
  const { userProfile } = useContext(UserProContext);
  const columns = [
    { selector: "user_id", name: "ID", sortable: true ,width:"10%"},
    { selector: "user_name", name: "Name", sortable: true ,width:"10%"},
    { selector: "user_mail", name: "EMail", sortable: true ,width:"20%"},
    { selector: "user_mobile", name: "Mobile", sortable: true ,width:"15%"},
    { selector: "user_reg_date", name: "RegDate", sortable: true ,width:"18%"},
    { selector: "availability", name: "Availability" , sortable: true ,width:"15%" },
    { selector: "user_status", name: "Status", sortable: true ,width:"10%"},
    {
      cell: (row) => (
        <IconButton>
          <EditOutlinedIcon onClick={() => editClick(`${row.user_id}`)} />
        </IconButton>
      ),
      name: "Edit",
    },
    {
      cell: (row) => (
        <IconButton>
           <DeleteOutlineOutlinedIcon onClick={() => deleteClick(`${row.user_id}`)} />
        </IconButton>
      ),
      name: "Delete",
    },
  ];
  const clientColumns=[
    { id: "user_name", label: "Client_Name" },
  ]
  const tableData = {
    columns,
    data,
  };
  return (
    <CustomContainer maxWidth="lg">
      <CustomBox m={10}>
        <CustomGrid
          container
          direction="row"
          justifyContent="space-between"
          alignItems="center"
        >
          <CustomTypography variant="h4">Finance</CustomTypography>
          {userProfile.user_cat_id === 301 && (
            <CustomButton
              size="small"
              variant="contained"
              label="Add Finance"
              onClick={() => {
                setOpen_add(true);
              }}
            />
          )}
        </CustomGrid>
        <CustomBox
          m={6}
          sx={{ height: 500, display: "flex", overflow: "hidden" }}
        >
          <DataTableExtensions
            print={false}
            export={false}
            columns={columns}
            filterPlaceholder="Search..."
            {...tableData}
          >
            <DataTable
              columns={columns}
              data={data}
              print={false}
              pagination
              highlightOnHover
            />
          </DataTableExtensions>
        </CustomBox>
        <CustomDialog open={open_edit} onClose={() => setOpen_edit(false)}>
          <CustomTypography variant="h5" align="center">
            Edit Details
          </CustomTypography>
          <form autoComplete="off" onSubmit={formikEdit.handleSubmit}>
            <CustomGrid
              container
              direction="column"
              justifyContent="center"
              alignItems="center"
              spacing={2}
            >
              <CustomTextField
                size="small"
                id="outlined-basic"
                label="Name"
                variant="outlined"
                onChange={formikEdit.handleChange}
                name="name"
                value={formikEdit.values.name}
                error={
                  formikEdit.touched.name && Boolean(formikEdit.errors.name)
                }
                onBlur={formikEdit.handleBlur}
                sx={{ m: 2 }}
              />
              {formikEdit.touched.name && formikEdit.errors.name ? (
                <div className="texterror">
                  <CustomTypography variant="caption" color="red">
                    {formikEdit.errors.name}
                  </CustomTypography>
                </div>
              ) : null}
              <CustomTextField
                size="small"
                label="Email"
                variant="outlined"
                onChange={formikEdit.handleChange}
                name="email"
                value={formikEdit.values.email}
                sx={{ m: 2 }}
                error={
                  formikEdit.touched.email && Boolean(formikEdit.errors.email)
                }
                onBlur={formikEdit.handleBlur}
              />
              {formikEdit.touched.email && formikEdit.errors.email ? (
                <div className="texterror">
                  <CustomTypography variant="caption" color="red">
                    {formikEdit.errors.email}
                  </CustomTypography>
                </div>
              ) : null}
              <CustomTextField
                size="small"
                label="Mobile"
                variant="outlined"
                onChange={formikEdit.handleChange}
                name="mobile"
                value={formikEdit.values.mobile}
                error={
                  formikEdit.touched.mobile && Boolean(formikEdit.errors.mobile)
                }
                onBlur={formikEdit.handleBlur}
                sx={{ m: 2 }}
              />
              {formikEdit.touched.mobile && formikEdit.errors.mobile ? (
                <div className="texterror">
                  <CustomTypography variant="caption" color="red">
                    {formikEdit.errors.mobile}
                  </CustomTypography>
                </div>
              ) : null}
              <FormikButton
                size="small"
                variant="contained"
                fullWidth
                type="submit"
                color="success"
                sx={{ m: 2 }}
                disabled={!formikEdit.isValid}
              >
                Update
              </FormikButton>
            </CustomGrid>
          </form>
        </CustomDialog>
        <CustomDialog open={open_add} onClose={() => setOpen_add(false)}>
          <CustomTypography variant="h5" align="center">
            Add Finance
          </CustomTypography>
          <form autoComplete="off" onSubmit={formikAdd.handleSubmit}>
            <CustomGrid
              container
              direction="column"
              justifyContent="space-between"
              alignItems="center"
            >
              <CustomTextField
                size="small"
                type="text"
                name="name"
                label="Name"
                value={formikAdd.values.name}
                onChange={formikAdd.handleChange}
                error={formikAdd.touched.name && Boolean(formikAdd.errors.name)}
                onBlur={formikAdd.handleBlur}
                sx={{ m: 2 }}
                style={{ width: "250px" }}
              />
              {formikAdd.touched.name && formikAdd.errors.name ? (
                <div className="texterror">
                  <CustomTypography variant="caption" color="red">
                    {formikAdd.errors.name}
                  </CustomTypography>
                </div>
              ) : null}
              <CustomTextField
                size="small"
                type="text"
                name="email"
                label="Emailid"
                value={formikAdd.values.email}
                sx={{ m: 2 }}
                style={{ width: "250px" }}
                onChange={formikAdd.handleChange}
                error={
                  formikAdd.touched.email && Boolean(formikAdd.errors.email)
                }
                onBlur={formikAdd.handleBlur}
              />
              {formikAdd.touched.email && formikAdd.errors.email ? (
                <div className="texterror">
                  <CustomTypography variant="caption" color="red">
                    {formikAdd.errors.email}
                  </CustomTypography>
                </div>
              ) : null}
              <CustomTextField
                size="small"
                type="number"
                name="mobile"
                label="Mobile Number"
                value={formikAdd.values.mobile}
                onChange={formikAdd.handleChange}
                error={
                  formikAdd.touched.mobile && Boolean(formikAdd.errors.mobile)
                }
                onBlur={formikAdd.handleBlur}
                sx={{ m: 2 }}
                style={{ width: "250px" }}
              />{" "}
              {formikAdd.touched.mobile && formikAdd.errors.mobile ? (
                <div className="texterror">
                  <CustomTypography variant="caption" color="red">
                    {formikAdd.errors.mobile}
                  </CustomTypography>
                </div>
              ) : null}
              <CustomTextField
                style={{ width: "250px" }}
                size="small"
                type="password"
                name="pwd"
                label="Password"
                value={formikAdd.values.pwd}
                onChange={formikAdd.handleChange}
                error={formikAdd.touched.pwd && Boolean(formikAdd.errors.pwd)}
                onBlur={formikAdd.handleBlur}
                sx={{ m: 2 }}
              />
              {formikAdd.touched.pwd && formikAdd.errors.pwd ? (
                <div className="texterror">
                  <CustomTypography variant="caption" color="red">
                    {formikAdd.errors.pwd}
                  </CustomTypography>
                </div>
              ) : null}
              <FormikButton
                size="small"
                variant="contained"
                type="submit"
                sx={{ m: 2 }}
                style={{ width: "80%" }}
                disabled={
                  !(formikAdd.isValid && formikAdd.dirty) ||
                  formikAdd.isSubmitting
                }
              >
                Add Finance
              </FormikButton>
            </CustomGrid>
          </form>
        </CustomDialog>
        <CustomDialog
          open={open_client}
          onClose={() => {
            setOpen_client(false);
          }}
        >
          <CustomTypography variant="h5" align="center">
            Edit Client
          </CustomTypography>
          <form autoComplete="off" onSubmit={formikClientEdit.handleSubmit}>
            <CustomGrid
              container
              direction="column"
              justifyContent="center"
              alignItems="center"
              spacing={2}
            >
              <CustomTextField
                select
                size="small"
                type="text"
                name="finance"
                label="Finance"
                value={formikClientEdit.values.finance}
                onChange={formikClientEdit.handleChange}
                error={
                  formikClientEdit.touched.finance &&
                  Boolean(formikClientEdit.errors.finance)
                }
                onBlur={formikClientEdit.handleBlur}
                sx={{ m: 2 }}
                style={{ width: "250px" }}
              >
                {console.log(finance)}
                {finance.map((finance) => (
                  <MenuItem value={finance}>{finance.name}</MenuItem>
                ))}
              </CustomTextField>
              {formikClientEdit.touched.finance &&
              formikClientEdit.errors.finance ? (
                <div className="texterror">
                  <CustomTypography variant="caption" color="red">
                    {formikClientEdit.errors.finance}
                  </CustomTypography>
                </div>
              ) : null}
              <FormikButton
                size="small"
                variant="contained"
                type="submit"
                sx={{ m: 2 }}
                style={{ width: "80%" }}
                disabled={
                  !(formikClientEdit.isValid && formikClientEdit.dirty) ||
                  formikClientEdit.isSubmitting
                }
              >
                Tag Client
              </FormikButton>
            </CustomGrid>
          </form>
        </CustomDialog>
        <CustomDialog open={open_tag} onClose={() => setOpen_tag(false)}>
          <CustomBox width={500}>
            <CustomTypography variant="h5" align="center">
              Client Details
            </CustomTypography>
            <br />
            <CustomClientTable
              stickyHeader
              size="small"
              columns={clientColumns}
              data={client}
              editClick={editClient}
              // deleteClick={deleteClick}
            />
          </CustomBox>
        </CustomDialog>
      </CustomBox>
    </CustomContainer>
  );
};
