// API fetching for Admin data
import React, { useContext, useState } from 'react'
import axios from 'axios'
import { useEffect } from 'react'
import { CategoryContext, UserContext ,UserProContext } from '../context/context'

export const useUserData = (url) => {
  const [tkn, setTkn] = useState("")
  const { user, setUser } = useContext(UserContext);
  const {category} = useContext(CategoryContext);
  const {userProfile,setUserProfile} =useContext(UserProContext)

  useEffect (() => {
    const formdata = new FormData();
    console.log(category);
    formdata.append("ucatid",category.id)
    formdata.append("uaddedby",userProfile.user_id)
     axios.post(url,formdata).then(function (res) {
      if (res.data.status === true) {
        setTkn(localStorage.getItem("tkn"))
        console.log(res.data.data);
        setUser(res.data.data);
      } else {
        console.log("Connection Error")
      }
    });
  }, [])
  return [user, tkn]
}

