import React from "react";
import { IconButton } from "@mui/material";
import CustomTable from "../Reusable/CustomTable";
import { useNavigate } from "react-router-dom";
import VisibilityOutlinedIcon from "@mui/icons-material/VisibilityOutlined";
import CustomGrid from "../Reusable/CustomGrid";
import CustomContainer from "../Reusable/CustomContainer";
import CustomTypography from "../Reusable/CustomTypography";
import CustomBox from "../Reusable/CustomBox";
import DataTable from "react-data-table-component";
import DataTableExtensions from "react-data-table-component-extensions";
import "react-data-table-component-extensions/dist/index.css";
import { useUser_SAdmin } from "./UserSAdminFunction";

export const ClientSAdmin=()=>{
  const [data] =useUser_SAdmin("/api/select_client_wrt_superadmin")
  const navigate = useNavigate()
  const columns = [
    { selector: "user_name", name: "Name", sortable: true ,width:"10%" },
    { selector: "user_mail", name: "EMail", sortable: true ,width:"15%" },
    { selector: "user_mobile", name: "Mobile", sortable: true ,width:"15%" },
    { selector: "user_reg_date", name: "RegDate", sortable: true ,width:"15%" },
    { selector: "cct_name", name: "CCTeam", sortable: true ,width:"12%" },
    { selector: "management_name", name: "Management", sortable: true ,width:"12%" },
    { selector: "finance_name", name: "Finance", sortable: true ,width:"12%" },
    {
      cell: () => (
        <IconButton>
          <VisibilityOutlinedIcon  onClick={() => {
        navigate("/dashboard/patients")
                  }} />
        </IconButton>
      ),
      name: "View",
    },
  ];
      const tableData = {
        columns,
        data,
      };
      return (
        <CustomContainer maxWidth="lg">
          <CustomBox m={10}>
            <CustomGrid
              container
              direction="row"
              justifyContent="space-between"
              alignItems="center"
            >
              <CustomTypography variant="h4">Clients</CustomTypography>
            </CustomGrid>
            <CustomBox
              m={6}
              sx={{ height: 500, display: "flex", overflow: "hidden" }}
            >
               <DataTableExtensions
            print={false}
            export={false}
            columns={columns}
            filterPlaceholder="Search..."
            {...tableData}
          >
            <DataTable
              columns={columns}
              data={data}
              print={false}
              pagination
              highlightOnHover
            />
          </DataTableExtensions>
            </CustomBox>
          </CustomBox>
        </CustomContainer>
      );
}