import { useContext, useState } from "react";
import { useFormik } from "formik";
import * as yup from "yup";
import {
  SnackContext,
  UserProContext,
  UserContext,
  CategoryContext,
} from "../context/context";
import axios from "axios";

export const useDeleteCCTeam = () => {
  const { setSnack } = useContext(SnackContext);
  const { user, setUser } = useContext(UserContext);
  const { category } = useContext(CategoryContext);
  const [open_tag, setOpen_tag] = useState(false);
  const [open_client, setOpen_client] = useState(false);
  const [cctid_old, setCctid_old] = useState("");
  const [client, setClient] = useState([]);
  const [ccteam, setCcteam] = useState([]);
  const [client_1, setClient_1] = useState({
    id: "",
    cct_id: "",
    cct_name: "",
  });
  const { userProfile } = useContext(UserProContext);
  const getClient = (id) => {
    setOpen_tag(true);
    const formdata = new FormData();
    formdata.append("cctid", id);
    axios.post("/api/select_cct", formdata).then(function (res) {
      console.log(res.data);
      if (res.data.status === true) {
        console.log(res.data.data[0].cct_id);
        setClient(res.data.data);
      }
    });
  };
  const deleteCCTeam = (id) => {
    console.log(id);
    const formdata = new FormData();
    formdata.append("userid", id);
    axios.post("/api/delete_cct", formdata).then(function (res) {
      console.log(res.data);
      if (res.data.status === true) {
        console.log(res);
        console.log(res.data);
        setSnack({
          message: res.data.msg,
          type: "success",
          open: true,
        });
        getdata()
      } else {
        console.log(res.data.data.user_id);
        getAvailability();
        getClient(`${res.data.data.user_id}`);
        setSnack({
          message: res.data.msg,
          type: "success",
          open: true,
        });
      }
    });
  };
  const getSingleClient = (id) => {
    const formdata = new FormData();
    console.log(id);
    formdata.append("clientid", id);
    console.log("bhujhb");
    axios.post("/api/select_single_client", formdata).then(function (res) {
      if (res.data.status === true) {
        console.log(res.data.data.cct_name);
        setClient_1({
          id: res.data.data.client_id,
          cct_id: res.data.data.cct_id,
          cct_name: res.data.data.cct_name,
        });
        setCctid_old(res.data.data.cct_id);
      } else {
        setClient_1({
          data: [],
          noData: false,
        });
      }
    });
  };

  const getAvailability = () => {
    axios.post("/api/user_select1").then(function (res) {
      if (res.data.status === true) {
        console.log(res.data.data);
        var id = userProfile.user_id;
        console.log(id);
        res.data.data.map((data) => {
          if (
            data.user_cat_id === 401 &&
            data.availability === 1 &&
            data.user_status=== 1 &&
            data.user_added_by == userProfile.user_id
          ) {
            var cc_team = { id: data.user_id, name: data.user_name };
            console.log(cc_team);
            ccteam.push(cc_team);
          }
          console.log(ccteam);
        });
      }
    });
  };
  const formikClientEdit = useFormik({
    initialValues: {
      id: client_1.id,
      ccteam: { id: client_1.cct_id, name: client_1.cct_name },
    },
    validationSchema: yup.object({
      ccteam: yup.object({
        id:yup.string().required("Select Command Center Team"),
        name:yup.string().required("Select Command Center Team"),
      }),
    }), 
    enableReinitialize: true,
    onSubmit: (values) => {
      console.log(values);
      console.log("============================");
      const formdata = new FormData();
      formdata.append("clientid", values.id);
      formdata.append("cctid", values.ccteam.id);
      formdata.append("useridnew", values.ccteam.id);
      formdata.append("cctname", values.ccteam.name);
      formdata.append("useridold", cctid_old);
      axios.post("/api/update_single_cctclient", formdata).then(function (res) {
        console.log(res);
        console.log("==================update====================");
        if (res?.data?.status === true) {
          setSnack({
            message: res.data.msg,
            type: "success",
            open: true,
          });
          setOpen_client(false);
          setCcteam([])
          getdata();
        } else {
          console.log("Error");

          setSnack({
            message: res.data.msg,
            type: "success",
            open: true,
          });
          setOpen_client(false);
        }
      });
    },
  });
  const deleteClick = (data) => {
    console.log(data);
    deleteCCTeam(`${data}`);
  };
  const editClient = (data) => {
    // getAvailability();
    console.log(data, "hi");
    setOpen_client(true);
    getSingleClient(`${data}`);
    setOpen_tag(false);
  };
  const getdata = () => {
    const formdata = new FormData();
    formdata.append("ucatid", category.id);
    formdata.append("uaddedby", userProfile.user_id);
    axios.post("/api/user_select", formdata).then(function (res) {
      if (res.data.status === true) {
        setUser(res.data.data);
      }
    });
  };
  return [
    deleteClick,
    open_tag,
    setOpen_tag,
    client,
    editClient,
    open_client,
    setOpen_client,
    formikClientEdit,
    ccteam,
  ];
};
