// import './App.css';
import Otp from "./Login/Otp/Otp"
import React, { useState, useEffect } from "react";
import { UserProContext, SnackContext, UserContext, CategoryContext } from "./context/context";
import {
  BrowserRouter as Router,
  Routes,
  Route,
  Navigate,
} from "react-router-dom";
import { Snackbar, Slide, Alert } from "@mui/material";
import CustomTypography from "./Reusable/CustomTypography";
import CustomBox from "./Reusable/CustomBox";
import Dashboard from "./Dashboard/Dashboard"
import Login from "./Login/Login/Login";
import ForgotPass from "./Login/ForgotPassword/ForgotPass"
import axios from "axios";

function App() {
  const [userProfile, setUserProfile] = useState({});
  const [category,setCategory] = useState({
    id:"",
    name:""
  });
  const[user,setUser] = useState([]);
  const [loading, setLoading] = useState(true);
  const [snack, setSnack] = useState({
    message: "",
    color: "",
    open: false,
  });

  useEffect(() => {
    checkSession();
  }, []);
  const checkSession = async () => {
      setLoading(true);
      console.log("before")
    await axios.post("/api/getsession").then(function (res) {
      console.log("after")
      if (res.data.status === true) {
        console.log(res.data.data);
        setUserProfile(res.data.data);
         setLoading(false)
      } else {
        setUserProfile(null);
        setLoading(false)
      }
      console.log(userProfile);
    });
  };

  return (
    <div className="App">
      <Snackbar
        autoHideDuration={3000}
        open={snack.open}
        onClose={() => {
          setSnack((prevdata) => {
            return {
              ...prevdata,
              open: false,
            };
          });
        }}
        TransitionComponent={Slide}
      >
        <Alert
          variant="filled"
          onClose={() => {
            setSnack((prevdata) => {
              return {
                ...prevdata,
                open: false,
              };
            });
          }} 
          severity={snack.type}
        >
          {snack.message}
        </Alert>
      </Snackbar>
      {loading?<CustomBox m={25}><CustomTypography variant="h4">Loading...</CustomTypography></CustomBox>:
      <CategoryContext.Provider value={{category,setCategory}}>
      <UserProContext.Provider value={{ userProfile, setUserProfile }}>
        <UserContext.Provider value={{ user, setUser }}>
          <SnackContext.Provider value={{ snack, setSnack }}>
            <Router>
              <Routes>
               <Route path="/" element={userProfile? <Navigate to="/dashboard" />:<Login />}/>
               <Route path="/dashboard/*" element = {userProfile? <Dashboard />:<Navigate to ="/" />}/>
                <Route path="/otp" element={<Otp />} />
                <Route path="/forgot_pass" element={<ForgotPass />} />
              </Routes>
            </Router>
          </SnackContext.Provider>
        </UserContext.Provider>
      </UserProContext.Provider>
      </CategoryContext.Provider>
}
    </div>
  );
}

export default App;
