import React, { useState } from "react";
import { styled, useTheme } from "@mui/material/styles";
import Box from "@mui/material/Box";
import Drawer from "@mui/material/Drawer";
import CssBaseline from "@mui/material/CssBaseline";
import MuiAppBar from "@mui/material/AppBar";
import Toolbar from "@mui/material/Toolbar";
import List from "@mui/material/List";
import CustomButton from "../Reusable/CustomButton";
import CustomTypography from "../Reusable/CustomTypography";
import Divider from "@mui/material/Divider";
import IconButton from "@mui/material/IconButton";
import MenuIcon from "@mui/icons-material/Menu";
import ChevronLeftIcon from "@mui/icons-material/ChevronLeft";
import ListItem from "@mui/material/ListItem";
import ListItemButton from "@mui/material/ListItemButton";
import ListItemIcon from "@mui/material/ListItemIcon";
import ListItemText from "@mui/material/ListItemText";
import MenuItem from "@mui/material/MenuItem";
import Menu from "@mui/material/Menu";
import { Button, Grid, Popover } from "@mui/material";
import AccountCircle from "@mui/icons-material/AccountCircle";
import { Link, useNavigate } from "react-router-dom";
import CustomBox from "../Reusable/CustomBox";
import {
  CategoryContext,
  SnackContext,
  UserProContext,
} from "../context/context";
import {
  super_admin_pages,
  admin_pages,
  cs_team_pages,
  Management_pages,
  CCTeam_pages,
  finance_pages,
  Client_pages
} from "./LeftDrawerPages";
import { useContext } from "react";
import axios from "axios";
import "../App.css";

const drawerWidth = 260;

const AppBar = styled(MuiAppBar, {
  shouldForwardProp: (prop) => prop !== "open",
})(({ theme, open }) => ({
  transition: theme.transitions.create(["margin", "width"], {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.leavingScreen,
  }),
  ...(open && {
    width: `calc(100% - ${drawerWidth}px)`,
    marginLeft: `${drawerWidth}px`,
    transition: theme.transitions.create(["margin", "width"], {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
  }),
}));

const DrawerHeader = styled("div")(({ theme }) => ({
  display: "flex",
  alignItems: "center",
  padding: theme.spacing(0, 1),
  // necessary for content to be below app bar
  ...theme.mixins.toolbar,
  justifyContent: "flex-end",
}));

export default function PersistentLeftDrawer() {
  const [open, setOpen] = useState(false);
  const [open_pop, setOpen_pop] = useState(false);
  const [auth, setAuth] = useState(true);
  const [anchorEl, setAnchorEl] = useState(null);
  const [anchorEl_pop, setAnchorEl_pop] = useState(null);
  const { setSnack } = useContext(SnackContext);
  const { userProfile, setUserProfile } = useContext(UserProContext);
  const { setCategory } = useContext(CategoryContext);
  const navigate = useNavigate();
  const theme = useTheme();

  const handleChange = (event) => {
    setAuth(event.target.checked);
  };

  const handleMenu = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const handleDrawerOpen = () => {
    setOpen(true);
  };

  const handleDrawerClose = () => {
    setOpen(false);
  };

  const popupClose = () => {
    setOpen_pop(false);
  };
  const logOutUser = async () => {
    if(userProfile.user_cat_id===501){
      const formdata = new FormData();
      formdata.append("uname", userProfile.user_mail);
      await axios.post("/api/client_logout", formdata).then(function (res) {
        if (res.data.status === true) {
          setSnack({
            message: res.data.msg,
            color: "green",
            type: "success",
            open: true,
          });
          localStorage.clear();
          setUserProfile(null);
          navigate("/");
        } else {
          setUserProfile(null);
          navigate("/");
          setSnack({
            message: res.data.msg,
            color: "error",
            type: "error",
            open: true,
          });
        }
        setOpen_pop(false);
      });
    }else{
    const formdata = new FormData();
    formdata.append("uname", userProfile.user_mail);
    await axios.post("/api/logout", formdata).then(function (res) {
      if (res.data.status === true) {
        setSnack({
          message: res.data.msg,
          color: "green",
          type: "success",
          open: true,
        });
        localStorage.clear();
        setUserProfile(null);
        navigate("/");
      } else {
        setUserProfile(null);
        navigate("/");
        setSnack({
          message: res.data.msg,
          color: "error",
          type: "error",
          open: true,
        });
      }
      setOpen_pop(false);
    });
  }
  };

  return (
    <CustomBox sx={{ display: "flex" }}>
      <CssBaseline />
      <AppBar position="fixed" open={open}>
        <Toolbar>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            onClick={handleDrawerOpen}
            edge="start"
            sx={{ mr: 2, ...(open && { display: "none" }) }}
          >
            <MenuIcon />
          </IconButton>
          <CustomTypography variant="h6" noWrap component="div">
            Dashboard
          </CustomTypography>
          <CustomBox flexGrow={1} />
          <CustomBox sx={{ display: { xs: "none", md: "flex" } }}>
            <div>
              <Grid
                container
                direction="row"
                justifyContent="space-between"
                alignItems="center"
              >
                <CustomTypography variant="h5">
                  {userProfile.user_cat}
                </CustomTypography>
                <IconButton
                  size="large"
                  aria-label="account of current user"
                  aria-controls="menu-appbar"
                  aria-haspopup="true"
                  onClick={handleMenu}
                  color="inherit"
                >
                  <AccountCircle />
                </IconButton>

                <Menu
                  id="menu-appbar"
                  anchorEl={anchorEl}
                  anchorOrigin={{
                    vertical: "top",
                    horizontal: "right",
                  }}
                  keepMounted
                  transformOrigin={{
                    vertical: "top",
                    horizontal: "right",
                  }}
                  open={Boolean(anchorEl)}
                  onClose={handleClose}
                >
                  <MenuItem onClick={handleClose}>
                    {userProfile.user_name}
                  </MenuItem>
                  <MenuItem
                    onClick={() => {
                      setOpen_pop(true);
                    }}
                  >
                    Logout
                  </MenuItem>
                  <Popover
                    open={open_pop}
                    onClose={popupClose}
                    anchorOrigin={{
                      vertical: "top",
                      horizontal: "right",
                    }}
                  >
                    <CustomBox p={2}>
                      <CustomTypography sx={{ p: 1 }}>
                        Are you sure to Logout
                      </CustomTypography>
                      &nbsp;{" "}
                      <CustomButton
                        size="small"
                        variant="contained"
                        color="success"
                        label="Yes"
                        onClick={logOutUser}
                      />
                      &nbsp;&nbsp;
                      <CustomButton
                        size="small"
                        variant="contained"
                        label="No"
                        color="error"
                        onClick={popupClose}
                      />
                    </CustomBox>
                  </Popover>
                </Menu>
              </Grid>
            </div>
          </CustomBox>
        </Toolbar>
      </AppBar>
      <Drawer
        sx={{
          width: drawerWidth,
          flexShrink: 0,
          "& .MuiDrawer-paper": {
            width: drawerWidth,
            boxSizing: "border-box",
          },
        }}
        variant="persistent"
        anchor="left"
        open={open}
      >
        <DrawerHeader>
          <IconButton onClick={handleDrawerClose}>
            <ChevronLeftIcon />
          </IconButton>
        </DrawerHeader>
        <Divider />
        {userProfile.user_cat_id === 101 && (
          <List>
            {super_admin_pages.map((page) => (
              <ListItem>
                <ListItemButton
                  onClick={() => {
                    navigate(`/dashboard/${page.linkname}`);
                    setCategory(page.category);
                    handleDrawerClose();
                  }}
                >
                  <ListItemIcon>
                    <MenuItem>
                      <CustomTypography textAlign="center">
                        {page.icon}
                      </CustomTypography>
                    </MenuItem>
                  </ListItemIcon>
                  <ListItemText primary={page.title} />
                </ListItemButton>
              </ListItem>
            ))}
          </List>
        )}
        {userProfile.user_cat_id === 201 && (
          <List>
            {admin_pages.map((page) => (
              <ListItem>
                <ListItemButton
                  onClick={() => {
                    navigate(`/dashboard/${page.linkname}`);
                    setCategory(page.category);
                    handleDrawerClose();
                  }}
                >
                  <ListItemIcon>
                    <MenuItem>
                      <CustomTypography textAlign="center">
                        {page.icon}
                      </CustomTypography>
                    </MenuItem>
                  </ListItemIcon>
                  <ListItemText primary={page.title} />
                </ListItemButton>
              </ListItem>
            ))}
          </List>
        )}
        {userProfile.user_cat_id === 301 && (
          <List>
            {cs_team_pages.map((page) => (
              <ListItem>
                <ListItemButton
                  onClick={() => {
                    navigate(`/dashboard/${page.linkname}`);
                    setCategory(page.category);
                    handleDrawerClose();
                  }}
                >
                  <ListItemIcon>
                    <MenuItem>
                      <CustomTypography textAlign="center">
                        {page.icon}
                      </CustomTypography>
                    </MenuItem>
                  </ListItemIcon>
                  <ListItemText primary={page.title} />
                </ListItemButton>
              </ListItem>
            ))}
          </List>
        )}
        {userProfile.user_cat_id === 401 && (
          <List>
            {CCTeam_pages.map((page) => (
              <ListItem>
                <ListItemButton
                  onClick={() => {
                    navigate(`/dashboard/${page.linkname}`);
                    setCategory(page.category);
                    handleDrawerClose();
                  }}
                >
                  <ListItemIcon>
                    <MenuItem>
                      <CustomTypography textAlign="center">                       
                        {page.icon}
                      </CustomTypography>
                    </MenuItem>
                  </ListItemIcon>
                  <ListItemText primary={page.title} />
                </ListItemButton>
              </ListItem>
            ))}
          </List>
        )}
         {userProfile.user_cat_id === 501 && (
          <List>
            {Client_pages.map((page) => (
              <ListItem>
                <ListItemButton
                  onClick={() => {
                    navigate(`/dashboard/${page.linkname}`);
                    setCategory(page.category);
                    handleDrawerClose();
                  }}
                >
                  <ListItemIcon>
                    <MenuItem>
                      <CustomTypography textAlign="center">                       
                        {page.icon}
                      </CustomTypography>
                    </MenuItem>
                  </ListItemIcon>
                  <ListItemText primary={page.title} />
                </ListItemButton>
              </ListItem>
            ))}
          </List>
        )}
         {userProfile.user_cat_id === 601 && (
          <List>
            {finance_pages.map((page) => (
              <ListItem>
                <ListItemButton
                  onClick={() => {
                    navigate(`/dashboard/${page.linkname}`);
                    setCategory(page.category);
                    handleDrawerClose();
                  }}
                >
                  <ListItemIcon>
                    <MenuItem>
                      <CustomTypography textAlign="center">                       
                        {page.icon}
                      </CustomTypography>
                    </MenuItem>
                  </ListItemIcon>
                  <ListItemText primary={page.title} />
                </ListItemButton>
              </ListItem>
            ))}
          </List>
        )}
        {userProfile.user_cat_id === 701 && (
          <List>
            {Management_pages.map((page) => (
              <ListItem>
                <ListItemButton
                  onClick={() => {
                    navigate(`/dashboard/${page.linkname}`);
                    setCategory(page.category);
                    handleDrawerClose();
                  }}
                >
                  <ListItemIcon>
                    <MenuItem>
                      <CustomTypography textAlign="center">
                        
                        {page.icon}
                      </CustomTypography>
                    </MenuItem>
                  </ListItemIcon>
                  <ListItemText primary={page.title} />
                </ListItemButton>
              </ListItem>
            ))}
          </List>
        )}
        <Divider />
      </Drawer>
    </CustomBox>
  );
}
