import { useContext } from "react";
import { UserContext, SnackContext, CategoryContext,UserProContext } from "../context/context";
import axios from "axios";

// Delete Admin by ID

export const useDeleteClient = () => {
  const { setSnack } = useContext(SnackContext);
  const { setUser } = useContext(UserContext);
  const { category } = useContext(CategoryContext);
  const { userProfile } = useContext(UserProContext);

  const deleteUser = (id) => {
    console.log(id);
    const formdata = new FormData();
    formdata.append("clientid", id);
    axios.post("/api/delete_client", formdata).then(function (res) {
      if (res.data.status === true) {
        console.log(res.data);       
        console.log(res.data.data);
        const formdata1 = new FormData();
        formdata1.append("cctid", res.data.data.cct_id);
        formdata1.append("cstid", userProfile.user_id);
        formdata1.append("managementid", res.data.data.management_id);
        formdata1.append("financeid", res.data.data.finance_id);
        axios.post("/api/update_avaliability", formdata1).then(function (res) {
          console.log(res.data);
          if (res.data.status === true) {
            console.log(res);
            console.log(res.data);
            setSnack({
              message: res.data.msg,
              type: "success",
              open: true,
            });
            getdata();
          } else {
            setSnack({
              message: res.data.msg,
              type: "success",
              open: true,
            });
            getdata();
          }
        });
      }
    });
  };
  const DeleteClick = (data) => {
    console.log(data);
    deleteUser(`${data}`);
  };
  const getdata = () => {
    const formdata = new FormData();
    // formdata.append("ucatid", category.id);
    formdata.append("cstid",userProfile.user_id)
    axios.post("/api/select_new_client", formdata).then(function (res) {
      if (res.data.status === true) {
        setUser(res.data.data);
      }
    });
  };
  return [DeleteClick];
};
