import React,{useContext} from "react";
import CustomBox from "../../Reusable/CustomBox";
import CustomTable from "../../Reusable/CustomTable";
import { IconButton } from "@mui/material";
import CustomContainer from "../../Reusable/CustomContainer";
import CustomTypography from '../../Reusable/CustomTypography'
import  CustomGrid from "../../Reusable/CustomGrid";
import{useNavigate} from "react-router-dom"
import VisibilityOutlinedIcon from "@mui/icons-material/VisibilityOutlined";
import DataTable from "react-data-table-component";
import DataTableExtensions from "react-data-table-component-extensions";
import "react-data-table-component-extensions/dist/index.css";
import { useManagmentClient } from "./ManagmentclientFunction";
// import {columns} from "../../Utils/ClientColumns"

export const ManagmentClients=()=>{
    const [data] = useManagmentClient("/api/select_management_client")
    const navigate = useNavigate()
    const columns = [
      { selector: "client_id", name: "ID" , sortable: true ,width:"10%"},
      { selector: "user_name", name: "Name" , sortable: true ,width:"10%"},
      { selector: "user_mail", name: "EMail" , sortable: true ,width:"20%"},
      { selector: "user_mobile", name: "Mobile" , sortable: true ,width:"20%"},
      { selector: "user_reg_date", name: "RegDate" , sortable: true ,width:"20%"},
      { selector: "user_status", name: "Status" , sortable: true ,width:"10%"},
      {
        cell: () => (
          <IconButton>
            <VisibilityOutlinedIcon  onClick={() => {
          navigate("/dashboard/patients")
                    }} />
          </IconButton>
        ),
        name: "View",
      },
    ];
      const tableData = {
        columns,
        data,
      };
    return(
        <CustomContainer maxWidth="lg">
        <CustomBox m={10}>
          <CustomGrid
            container
            direction="row"
            justifyContent="space-between"
            alignItems="center"
          >
            <CustomTypography variant="h4">Clients</CustomTypography>
            </CustomGrid>
        <CustomBox
          m={6}
          sx={{ height: 500, display: "flex", overflow: "hidden" }}
        >
           <DataTableExtensions
            print={false}
            export={false}
            columns={columns}
            filterPlaceholder="Search..."
            {...tableData}
          >
          <DataTable
              columns={columns}
              data={data}
              print={false}
              pagination
              highlightOnHover
            /></DataTableExtensions>
        </CustomBox>
        </CustomBox>
        </CustomContainer>
    )
}