import React, { useContext, useState } from "react";
import axios from "axios";
import { useEffect } from "react";
import {
  CategoryContext,
  UserContext,
  UserProContext,
} from "../context/context";

export const useCCTeamAdmin = (url) => {
  const { user, setUser } = useContext(UserContext);
  const { category } = useContext(CategoryContext);
  const { userProfile, setUserProfile } = useContext(UserProContext);
  useEffect(() => {
    const formdata = new FormData();
    formdata.append("useradded1", userProfile.user_id);
    formdata.append("useradded2", userProfile.user_id);
    axios.post(url, formdata).then(function (res) {
      if (res.data.status === true) {
        console.log(res.data.data);
        console.log(res.data.data[0].cst_team);
        console.log(JSON.stringify(res.data.data[0].cst_team));
        const formdata1 = new FormData();
        formdata1.append("cst_team", JSON.stringify(res.data.data[0].cst_team));
           formdata1.append("ucatid",category.id)
        axios
          .post("/api/select_cctmf_cst_adddedbyadmin", formdata1)
          .then(function (res) {
            if (res.data.status === true) {
              console.log(res.data.data);
              setUser(res.data.data);
            }
          });
      }
    });
  }, []);
  return [user];
};
