import FormikButton from "../../Reusable/FormikButton";
import  CustomGrid from "../../Reusable/CustomGrid";
import { Button } from "@mui/material";
import CustomTypography from "../../Reusable/CustomTypography";
import CustomContainer from "../../Reusable/CustomContainer";
import CustomDialog from "../../Reusable/CustomDilog";
import CustomTextField from "../../Reusable/CustomTextfield";
import react, { useState } from "react";
import CustomBox from "../../Reusable/CustomBox";
import { usePassword } from "./ForgotPassFunction";

const ForgotPass = () => {
  const [formik, open, formik_mail] = usePassword();
  return (
    <CustomContainer>
      <CustomBox m={15} p={2} boxShadow={5}>
        <form onSubmit={formik_mail.handleSubmit}>
          <CustomGrid
            container
            direction="column"
            justifyContent="space-between"
            alignItems="center"
          >
            <CustomTextField
            
              fullwidth
              size="small"
              type="text"
              name="email"
              label="Email"
              value={formik_mail.values.email}
              onChange={formik_mail.handleChange}
            ></CustomTextField>
             <FormikButton
              size="small"
              variant="contained"
              type="submit"
              fullwidth
              sx={{ m: 2 }}
              disabled={!(formik_mail.isValid && formik_mail.dirty)}
            >
              next
             </FormikButton>
          </CustomGrid>
        </form>
      </CustomBox>
      <CustomDialog open={open}>
            <CustomBox p={2}>
              <form onSubmit={formik.handleSubmit}>
                <CustomGrid
                  container
                  direction="column"
                  justifyContent="space-between"
                  alignItems="center"
                >
                  <CustomTextField
                    fullwidth
                    size="small"
                    type="text"
                    name="email"
                    label="Email"
                    
                    disabled
                    value={formik.values.email}
                    onChange={formik.handleChange}
                    error={formik.touched.uname && Boolean(formik.errors.uname)}
                    onBlur={formik.handleBlur}
                    sx={{ m: 2 }}
                  />
                  {formik.touched.email && formik.errors.email ? (
                    <div className="texterror">
                      <CustomTypography variant="caption"  color="red">
                        {formik.errors.email}
                      </CustomTypography>
                    </div>
                  ) : null}

                  <CustomTextField
                    fullwidth
                    size="small"
                    type="password"
                    name="pwd"
                    label="Password"
                    value={formik.values.pwd}
                    onChange={formik.handleChange}
                    error={formik.touched.uname && Boolean(formik.errors.uname)}
                    onBlur={formik.handleBlur}
                    sx={{ m: 2 }}
                  />
                  {formik.touched.pwd && formik.errors.pwd ? (
                    <div className="texterror">
                      <CustomTypography variant="caption"  color="red">
                        {formik.errors.pwd}
                      </CustomTypography>
                    </div>
                  ) : null}

                  <CustomTextField
                    fullwidth
                    size="small"
                    type="password"
                    name="new_pwd"
                    label="Confirm Password"
                    value={formik.values.new_pwd}
                    onChange={formik.handleChange}
                    error={formik.touched.uname && Boolean(formik.errors.uname)}
                    onBlur={formik.handleBlur}
                    sx={{ m: 2 }}
                  />
                  {formik.touched.new_pwd && formik.errors.new_pwd ? (
                    <div className="texterror">
                      <CustomTypography variant="caption"  color="red">
                        {formik.errors.new_pwd}
                      </CustomTypography>
                    </div>
                  ) : null}

                   <Button
                    size="small"
                    variant="contained"
                    type="submit"
                    fullwidth
                    sx={{ m: 2 }}
                    disabled={
                      !(formik.isValid && formik.dirty) || formik.isSubmitting
                    }
                  >
                    next
                   </Button>
                </CustomGrid>
              </form>
            </CustomBox>
      </CustomDialog>
    </CustomContainer>
  );
};
export default ForgotPass;
