import React, { useContext, useEffect, useState } from "react";
import axios from "axios";
import {
    CategoryContext,
    SnackContext,
    UserContext,
    UserProContext,
  } from "../../context/context";
import { useFormik } from "formik";
import * as yup from "yup";
import { passwordRegExp, phoneRegExp } from "../../Utils/RegularExpressions"

export const useAddTeam = (url)=>{
    const [open_add, setOpen_add] = useState(false);
    const { setSnack } = useContext(SnackContext);
    const { setUser } = useContext(UserContext);
    const {category} = useContext(CategoryContext);
    const { userProfile } = useContext(UserProContext);
  const password = passwordRegExp;
  const phoneNumber = phoneRegExp;
  const formikAdd = useFormik({
    initialValues: {
      email: "",
      mobile: "",
      pwd: "",
      name: "",
    },
    validationSchema: yup.object({
        email: yup
          .string()
          .email("Enter a valid email")
          .required("Email is required"),
        mobile: yup
          .string()
          .matches(phoneNumber, "Mobilenumber is not valid")
          .min(10, "Enter a valid Mobilenumber of 10 numbers")
          .max(10, "Mobilenumber is not valid")
          .required("Mobilenumber is required"),
        pwd: yup
          .string()
          .matches(password, "Password is invalid")
          // .min(6, "Password is too short")
          .required("Enter Your Password"),
        name: yup
          .string()
          .min(2, "Too Short!")
          .max(50, "Too Long!")
          .required("Name is Required"),
      }),
      onSubmit: async (values, { resetForm }) => {
        console.log(values); 
        try {
            const formdata = new FormData();
            formdata.append("cctid",userProfile.user_id)
            formdata.append("cctname",userProfile.user_name)
            formdata.append("subcatid",category.id)
            formdata.append("subcat",category.name)
            formdata.append("subname",values.name)
            formdata.append("submobile",values.mobile)
            formdata.append("submail",values.email)
            formdata.append("subpass",values.pwd)
            console.log(formdata);
            await axios.post(url, formdata).then(function (res) {
              if (res.data.status === true) {
                console.log(res);
                console.log(res.data);
                setSnack({
                  message: res.data.msg,
                  type: "success",
                  open: true,
                });
                setOpen_add(false);
            } else {
                setSnack({
                  message: res.data.msg,
                  type: "success",
                  open: true,
                });
              }
                getdata();
              console.log("=============");
            });
          } catch (err) {
            console.log("Error");
          }
          resetForm({});
        },
      });
      const getdata = () => {
        const formdata = new FormData();
        formdata.append("usubcatid", category.id);
        formdata.append("cctid",userProfile.user_id)
        axios.post("/api/select_sub_cct", formdata).then(function (res) {
          if (res.data.status === true) {
            setUser(res.data.data);
          }
        });
      };
      return [formikAdd, open_add, setOpen_add];
}