import { useContext, useState } from "react";
import { useFormik } from "formik";
import * as yup from "yup";
import {
  SnackContext,
  UserProContext,
  UserContext,
  CategoryContext,
} from "../context/context";
import axios from "axios";

export const useDeleteAdmin = () => {
  const { setSnack } = useContext(SnackContext);
  const { userProfile } = useContext(UserProContext);
  const { user, setUser } = useContext(UserContext);
  const { category } = useContext(CategoryContext);
  const [csteam, setCsteam] = useState([]);
  const [admin_old, setAdmin_old] = useState();
  const [open_cs, setOpen_cs] = useState(false);
  const [open_tag, setOpen_tag] = useState(false);
  const [admin, setAdmin] = useState([]);
  const [csteam1, setCsteam1] = useState({
    id: "",
    admin: "",
  });
  const deleteAdmin = (id) => {
    console.log(id);
    const formdata = new FormData();
    formdata.append("userid", id);
    axios
      .post("/api/select_cst_to_deactivate_admin", formdata)
      .then(function (res) {
        console.log(res.data);
        if (res.data.status === true) {
          console.log(res);
          console.log(res.data);
          setSnack({
            message: res.data.msg,
            type: "success",
            open: true,
          });
          getdata();
        } else {
          console.log(res.data.data);
          setCsteam(res.data.data);
          setOpen_cs(true);
        }
      });
  };
  const getAvailability = () => {
    axios.post("/api/user_select1").then(function (res) {
      if (res.data.status === true) {
        console.log(res.data.data);
        var id = userProfile.user_id;
        console.log(id);
        res.data.data.map((data) => {
          if (
            data.user_cat_id === 201 &&
            data.availability === 1 &&
            data.user_status === 1&&
            data.user_added_by == userProfile.user_id
          ) {
            var admin1 = data.user_id;
            admin.push(admin1);
          }
          console.log(admin);
        });
      }
    });
  };
  const getSingleCsteam = (id) => {
    const formdata = new FormData();
    console.log(id);
    formdata.append("userid", id);
    console.log("bhujhb");
    axios
      .post("/api/select_single_cst_useraddedby", formdata)
      .then(function (res) {
        if (res.data.status === true) {
          console.log(res.data.data);
          setCsteam1({
            id: res.data.data.user_id,
            admin: res.data.data.user_added_by,
          });
          setAdmin_old(res.data.data.user_added_by);
        }
      });
  };
  const formikAdminEdit = useFormik({
    initialValues: {
      id: csteam1.id,
      admin: csteam1.admin,
    },
    validationSchema: yup.object({
      admin: yup.string().required("Select Admin"),
    }),
    enableReinitialize: true,
    onSubmit: (values) => {
      console.log(values);
      console.log("============================");
      const formdata = new FormData();
      formdata.append("userid", values.id);
      formdata.append("uaddedby", values.admin);
      axios.post("/api/update_cst_addedby", formdata).then(function (res) {
        console.log(res);
        console.log("==================update====================");
        if (res?.data?.status === true) {
          setSnack({
            message: res.data.msg,
            type: "success",
            open: true,
          });
          setOpen_tag(false);
          setAdmin([]);
          const formdata1 = new FormData();
          formdata1.append("userid", admin_old);
          axios
            .post("/api/select_cst_to_deactivate_admin", formdata1)
            .then(function (res) {
              console.log(res.data);
              if (res.data.status === true) {
                console.log(res);
                console.log(res.data);
                setSnack({
                  message: res.data.msg,
                  type: "success",
                  open: true,
                });
                getdata();
              }
            });
        } else {
          console.log("Error");
          setSnack({
            message: res.data.msg,
            type: "success",
            open: true,
          });
          setOpen_tag(false);
        }
      });
    },
  });
  const deleteAdminClick = (data) => {
    console.log(data, "hi");
    deleteAdmin(`${data}`);
  };
  const editCsteam = (data) => {
    getAvailability();
    console.log(data, "hi");
    setOpen_tag(true);
    getSingleCsteam(`${data}`);
    setOpen_cs(false);
  };
  const getdata = () => {
    const formdata = new FormData();
    formdata.append("ucatid", category.id);
    formdata.append("uaddedby", userProfile.user_id);
    axios.post("/api/user_select", formdata).then(function (res) {
      if (res.data.status === true) {
        setUser(res.data.data);
      }
    });
  };
  return [
    open_cs,
    setOpen_cs,
    open_tag,
    setOpen_tag,
    deleteAdminClick,
    csteam,
    editCsteam,
    formikAdminEdit,
    admin,
  ];
};
