import { useContext, useState } from "react";
import { useFormik } from "formik";
import * as yup from "yup";
import {
  UserContext,
  SnackContext,
  CategoryContext,
  UserProContext,
} from "../context/context";
import axios from "axios";

export const useDeleteCSTeam = () => {
  const [open_client, setOpen_client] = useState(false);
  const [open_emp, setOpen_emp] = useState(false);
  const [open_tag, setOpen_tag] = useState(false);
  const [client_data, setClient_data] = useState([]);
  const [emp_data,setEmp_data] =useState([])
  const [csteam_id_old, setCsteam_id_old] = useState("");
  const [csteam,setCsteam] =useState([])
  const { setSnack } = useContext(SnackContext);
  const { setUser } = useContext(UserContext);
  const { category } = useContext(CategoryContext);
  const { userProfile } = useContext(UserProContext);
  const [client_1, setClient_1] = useState({
    id: "",
    management_id: "",
    cct_id:"",
    finance_id:"",
    cst_id:""
  });
 const [emp1,setEmp1]= useState({
  id: "",
  name:""
 })
  const deleteUser = (id) => {
    console.log(id);
    const formdata = new FormData();
    formdata.append("userid", id);
    formdata.append("cstid", id);
    axios
      .post("/api/select_client_to_deactivate_cst", formdata)
      .then(function (res) {
        console.log(res.data);
        if (res.data.status === true) {
          console.log(res.data.status);
          setSnack({
            message: res.data.msg,
            type: "success",
            open: true,
          });
          getdata()
        } else {
          console.log(res.data.status);
          console.log(res.data.data[0]);
          setOpen_tag(true);
          setClient_data(res.data.data[0]);
         setEmp_data(res.data.data[1])
        }
      });
  };
  const getSingleClient = (id) => {
    const formdata = new FormData();
    console.log(id);
    formdata.append("clientid", id);
    console.log("bhujhb");
    axios.post("/api/select_single_client", formdata).then(function (res) {
      if (res.data.status === true) {
        console.log(res.data.data)
        setClient_1({
          id: res.data.data.client_id,
          management_id: res.data.data.management_id,
          cct_id:res.data.data.cct_id,
          finance_id:res.data.data.finance_id,
          cst_id:res.data.data.cst_id
        });
        setCsteam_id_old(res.data.data.cst_id);
      } else {
        setClient_1({
          data: [],
          noData: false,
        });
      }
      console.log(client_1)
    });
  };
  const getSingleUser = (id) => {
    const formdata = new FormData()
    console.log(id)
    formdata.append("user_id", id)
    console.log("bhujhb")
    axios.post("/api/single_user", formdata).then(function (res) {
        if (res.data.status === true) {
            console.log(res.data)
            setEmp1({
                id: res.data.data.user_id,
                csteam: res.data.data.user_added_by,
              })
            } else {
                setEmp1({
                    data: [],
                    noData: false,
                });
            }
        });
    };
  const getAvailability = () => {
    axios.post("/api/user_select1").then(function (res) {
      if (res.data.status === true) {
        console.log(res.data.data);
        var id = userProfile.user_id;
        console.log(id);
        res.data.data.map((data) => {
          if (
            data.user_cat_id === 301 &&
            data.availability === 1 &&
            data.user_status === 1&&
            data.user_added_by == userProfile.user_id
          ) {
            var cst =  data.user_id
            csteam.push(cst);
          }
          console.log(csteam);
        });
      }
    });
  };
  const formikClientEdit = useFormik({
    initialValues: {
      id: client_1.id,
      csteam:client_1.cst_id, 
      cct_id:client_1.cct_id, 
      management_id:client_1.management_id, 
      finance_id:client_1.finance_id, 
    },
    validationSchema: yup.object({
        csteam:yup.string().required("Select Customer Success Team"),
    }),
    enableReinitialize: true,
    onSubmit: (values) => {
      console.log(values);
      console.log("============================");
      const formdata = new FormData();
      formdata.append("clientid", values.id);
      formdata.append("userid1", values.finance_id);
      formdata.append("userid2", values.cct_id);
      formdata.append("userid3", values.management_id);
      formdata.append("uaddedby", values.csteam);
      formdata.append("cstid", values.csteam);
      formdata.append("userid", csteam_id_old);
      axios
        .post("/api/update_useraddedby_usertable", formdata)
        .then(function (res) {
          console.log(res);
          console.log("==================update====================");
          if (res?.data?.status === true) {
            setSnack({
              message: res.data.msg,
              type: "success",
              open: true,
            });
            setOpen_client(false);
            setCsteam([])
            getdata();
          } else {
            console.log("Error");
            setSnack({
              message: res.data.msg,
              type: "success",
              open: true,
            });
            setOpen_client(false);
          }
        });
    },
  });
  const formikEmpEdit =useFormik({
    initialValues: {
    id:emp1.id,
    csteam:emp1.csteam
    },
    validationSchema: yup.object({
      csteam:yup.string().required("Select Customer Success Team"),
  }),
  enableReinitialize: true,
    onSubmit: (values) => {
      console.log(values);
      console.log("============================");
      const formdata = new FormData();
      formdata.append("userid", values.id);
      formdata.append("uaddedby", values.csteam);
      axios
      .post("/api/update_useraddedby_to_deactivate_cst", formdata)
      .then(function (res) {
        console.log(res);
        console.log("==================update====================");
        if (res?.data?.status === true) {
          setSnack({
            message: res.data.msg,
            type: "success",
            open: true,
          });
          setCsteam([])
          setOpen_emp(false);
          getdata();
        } else {
          console.log("Error");
          setSnack({
            message: res.data.msg,
            type: "success",
            open: true,
          });
          setOpen_emp(false);
        }
      });
    }
  })
  const deleteClick = (data) => {
    console.log(data);
    deleteUser(`${data}`);
  };
  const clientEditClick = (data) => {
    getAvailability();
    console.log(data)
    setOpen_tag(false);
    getSingleClient(`${data}`);
    setOpen_client(true); 
};
const empEditClick=(data)=>{
  getAvailability();
  console.log(data)
  setOpen_tag(false);
  getSingleUser(`${data}`)
  setOpen_emp(true)
}
const getdata = () => {
  const formdata = new FormData();
  formdata.append("ucatid", category.id);
  formdata.append("uaddedby",userProfile.user_id)
  axios.post("/api/user_select", formdata).then(function (res) {
    if (res.data.status === true) {
      setUser(res.data.data);
    }
  });
};
  return [
    deleteClick,
    open_client,
    setOpen_client,open_emp, setOpen_emp,
    client_data,emp_data,
    clientEditClick,empEditClick,
    open_tag,
    setOpen_tag,csteam,formikClientEdit,formikEmpEdit
  ];
};
