import { useContext, useState } from "react";
import { useFormik } from "formik";
import * as yup from "yup";
import {
  SnackContext,
  UserProContext,
  UserContext,
  CategoryContext,
} from "../context/context";
import axios from "axios";

export const useDeleteFinance = () => {
  const { setSnack } = useContext(SnackContext);
  const { user, setUser } = useContext(UserContext);
  const { category } = useContext(CategoryContext);
  const [open_tag, setOpen_tag] = useState(false);
  const [open_client, setOpen_client] = useState(false);
  const [client, setClient] = useState([]);
  const [finance_id_old, setFinance_id_old] = useState("");
  const [finance, setFinance] = useState([]);
  const [client_1, setClient_1] = useState({
    id: "",
    finance_id: "",
    finance_name: "",
  });
  const { userProfile } = useContext(UserProContext);
  const getClient = (id) => {
    setOpen_tag(true);
    const formdata = new FormData();
    formdata.append("financeid", id);
    axios.post("/api/select_finance", formdata).then(function (res) {
      console.log(res.data);
      if (res.data.status === true) {
        console.log(res.data.data[0].finance_id);
        setClient(res.data.data);
      }
    });
  };
  const deleteFinance = (id) => {
    console.log(id);
    const formdata = new FormData();
    formdata.append("userid", id);
    axios.post("/api/delete_cct", formdata).then(function (res) {
      console.log(res.data);
      if (res.data.status === true) {
        console.log(res);
        console.log(res.data);
        setSnack({
          message: res.data.msg,
          type: "success",
          open: true,
        });
        getdata();
      } else {
        console.log(res.data.data.user_id);
        getAvailability();
        getClient(`${res.data.data.user_id}`);
        setSnack({
          message: res.data.msg,
          type: "success",
          open: true,
        });
      }
    });
  };
  const getSingleClient = (id) => {
    const formdata = new FormData();
    console.log(id);
    formdata.append("clientid", id);
    console.log("bhujhb");
    axios.post("/api/select_single_client", formdata).then(function (res) {
      if (res.data.status === true) {
        setClient_1({
          id: res.data.data.client_id,
          management_id: res.data.data.finance_id,
          finance_name: res.data.data.finance_name,
        });
        setFinance_id_old(res.data.data.finance_id);
      } else {
        setClient_1({
          data: [],
          noData: false,
        });
      }
    });
  };
  const getAvailability = () => {
    axios.post("/api/user_select1").then(function (res) {
      if (res.data.status === true) {
        console.log(res.data.data);
        var id = userProfile.user_id;
        console.log(id);
        res.data.data.map((data) => {
          if (
            data.user_cat_id === 601 &&
            data.availability === 1 &&
            data.user_status===1 &&
            data.user_added_by == userProfile.user_id
          ) {
            var finan = { id: data.user_id, name: data.user_name };
            finance.push(finan);
          }
          console.log(finance);
        });
      }
    });
  };

  const formikClientEdit = useFormik({
    initialValues: {
      id: client_1.id,
      finance: { id: client_1.finance_id, name: client_1.finance_name },
    },
    validationSchema: yup.object({
      finance: yup.object({
        id:yup.string().required("Select Finance"),
        name:yup.string().required("Select Finance"),
      }),
    }),
    enableReinitialize: true,
    onSubmit: (values) => {
      console.log(values);
      console.log("============================");
      const formdata = new FormData();
      formdata.append("clientid", values.id);
      formdata.append("financeid", values.finance.id);
      formdata.append("useridnew", values.finance.id);
      formdata.append("financename", values.finance.name);
      formdata.append("useridold", finance_id_old);
      axios
        .post("/api/update_single_financeclient", formdata)
        .then(function (res) {
          console.log(res);
          console.log("==================update====================");
          if (res?.data?.status === true) {
            setSnack({
              message: res.data.msg,
              type: "success",
              open: true,
            });
            setOpen_client(false);
            setFinance([])
            getdata();
          } else {
            console.log("Error");
            setSnack({
              message: res.data.msg,
              type: "success",
              open: true,
            });
            setOpen_client(false);
          }
        });
    },
  });
  const deleteClick = (data) => {
    console.log(data);
    deleteFinance(`${data}`);
  };
  const editClient = (data) => {
    // getAvailability();
    console.log(data, "hi");
    setOpen_client(true);
    getSingleClient(`${data}`);
    setOpen_tag(false);
  };
  const getdata = () => {
    const formdata = new FormData();
    formdata.append("ucatid", category.id);
    formdata.append("uaddedby", userProfile.user_id);
    axios.post("/api/user_select", formdata).then(function (res) {
      if (res.data.status === true) {
        setUser(res.data.data);
      }
    });
  };
  return [
    open_tag,
    setOpen_tag,
    open_client,
    setOpen_client,
    client,
    finance,
    formikClientEdit,
    editClient,
    deleteClick,
  ];
};
