import React from "react";
import OTPInput from "react-otp-input";
import  CustomGrid from "../../Reusable/CustomGrid";
import CustomDialog from "../../Reusable/CustomDilog";
import CustomBox from "../../Reusable/CustomBox";
import CustomButton from "../../Reusable/CustomButton";
import CircularProgress from "@mui/material/CircularProgress";
import { Paper, Avatar, IconButton, Link } from "@material-ui/core";
import CustomTypography from "../../Reusable/CustomTypography";
import { useOtp } from "./OtpFunction";
const Otp = () => {
  const paperStyle = {
    padding: 20,
    height: "50vh",
    width: 480,
    margin: "20px auto",
  };
  const avatarStyle = { backgroundColor: "#1bbd7e" };
  const btnstyle = { margin: "8px 0" };
  const [values, handleChange, handleSubmit, loading] = useOtp();

  return (
    <CustomBox m={15}>
      <form onSubmit={handleSubmit}>
        <Paper elevation={10} style={paperStyle}>
          <CustomGrid
            container
            direction="column"
            justifyContent="space-between"
            alignItems="center"
          >
            <CustomTypography variant="h4">OTP</CustomTypography>

            <OTPInput
              onChange={handleChange}
              value={values.otp}
              numInputs={6}
              should={true}
              separator={<span></span>}
              inputStyle={{
                margin: 20,
                width: 25,
                height: 25,
              }}
            />
            <br />
            <CustomButton
              size="small"
              label="submit"
              type="submit"
              color="primary"
              variant="contained"
              m={2}
              fullWidth
            />
            <CustomTypography>
              <Link href="#">Resend OTP</Link>
            </CustomTypography>
          </CustomGrid>
        </Paper>
      </form>
      <CustomDialog open={loading} onClose={() => {}}>
        <CustomGrid
          container
          direction="row"
          justifyContent="space-between"
          alignItems="center"
        >
          <IconButton>
            <CircularProgress />
          </IconButton>
          <CustomTypography variant="h5">Please Wait ...</CustomTypography>
        </CustomGrid>
      </CustomDialog>
    </CustomBox>
  );
};

export default Otp;
