import React, { useContext, useState } from "react";
import axios from "axios";
import { useFormik } from "formik";
import * as yup from "yup";
import { UserContext, SnackContext, CategoryContext, UserProContext } from "../context/context";

export const useEditClient = () => {
  const { setUser } = useContext(UserContext);
  const [client, setClient] = useState({
    id: "",
    name: "",
    email: "",
    mobile: "",
  });
  const { setSnack } = useContext(SnackContext);
  const { category } = useContext(CategoryContext);
  const {userProfile} = useContext(UserProContext)
  const [open_edit, setOpen_edit] = useState(false);

  // select client by using ID
  const getSingleClient = (id) => {
    const formdata = new FormData();
    console.log(id);
    formdata.append("clientid", id);
    axios.post("/api/client_single", formdata).then(function (res) {
      if (res.data.status === true) {
        console.log(res.data.data);
        setClient({
          id: res.data.data.client_id,
          name: res.data.data.user_name,
          mobile: res.data.data.user_mobile,
          email: res.data.data.user_mail,
        });
      } else {
        setClient({
          data: [],
          noData: false,
        });
      }
      console.log(client);
    });
    console.log(client);
  };
  const editClick = (data) => {
    console.log(data, "hi");
    getSingleClient(`${data}`);
    setOpen_edit(true);
  };
  // Update client by ID
  const formikEdit = useFormik({
    initialValues: {
      id: client.id,
      name: client.name,
      email: client.email,
      mobile: client.mobile,
      status: client.status,
    },
    enableReinitialize: true,
    validationSchema: yup.object({
      name: yup
        .string()
        .min(2, "Too Short!")
        .max(50, "Too Long!")
        .required("Name is Required"),
      email: yup
        .string()
        .email("Enter a valid email")
        .required("Email is required"),
      mobile: yup
        .string()
        .min(10, "Enter a valid Mobilenumber of 10 numbers")
        .max(10, "Mobilenumber is not valid")
        .required("Mobilenumber is required"),
    }),

    onSubmit: (values) => {
      console.log(values);
      console.log("============================");
      const formdata = new FormData();
      formdata.append("clientid", values.id);
      formdata.append("uname", values.name);
      formdata.append("umail", values.email);
      formdata.append("umobile", values.mobile);
      axios.post("/api/client_update", formdata).then(function (res) {
        console.log(res);
        console.log("==================update====================");
        if (res?.data?.status === true) {
          setSnack({
            message: res.data.msg,
            type: "success",
            open: true,
          });
          setOpen_edit(false);
        } else {
          console.log("Error");

          setSnack({
            message: res.data.msg,
            type: "success",
            open: true,
          });
          setOpen_edit(false);
        }
           getdata()
      });
    },
  });
  const getdata = () => {
    const formdata = new FormData();
    // formdata.append("ucatid", category.id);
    formdata.append("cstid", userProfile.user_id);
    axios.post("/api/select_new_client", formdata).then(function (res) {
      if (res.data.status === true) {
        setUser(res.data.data);
      }
    });
  };

  return [editClick, formikEdit, open_edit, setOpen_edit];
};
