import React from "react";
import CustomTable from "../Reusable/CustomTable";
import CustomGrid from "../Reusable/CustomGrid";
import CustomContainer from "../Reusable/CustomContainer";
import CustomTypography from "../Reusable/CustomTypography";
import CustomBox from "../Reusable/CustomBox";
import DataTable from "react-data-table-component";
import DataTableExtensions from "react-data-table-component-extensions";
import "react-data-table-component-extensions/dist/index.css";
import {useInActive} from "./InActiveFunction"

export const InActive=()=>{
    const[data]=useInActive("/api/select_inactive_users")
    const columns = [
        { selector: "user_id", name: "ID" , sortable: true ,width:"10%"},
        { selector: "user_name", name: "Name" , sortable: true ,width:"10%"},
        { selector: "user_mail", name: "EMail" , sortable: true ,width:"20%"},
        { selector: "user_mobile", name: "Mobile" , sortable: true ,width:"15%"},
        { selector: "user_reg_date", name: "RegDate" , sortable: true ,width:"16%"},
        { selector: "user_status", name: "Status" , sortable: true ,width:"10%"},
        {selector:"user_cat",name:"Category", sortable: true ,width:"20%"}  
    ];
    const tableData = {
      columns,
      data,
    };
      return (
        <CustomContainer maxWidth="lg">
          <CustomBox m={10}>
            <CustomGrid
              container
              direction="row"
              justifyContent="space-between"
              alignItems="center"
            >
              <CustomTypography variant="h4">InActive Members</CustomTypography>
            </CustomGrid>
            <CustomBox
              m={6}
              sx={{ height: 500, display: "flex", overflow: "hidden" }}
            >
               <DataTableExtensions
            print={false}
            export={false}
            columns={columns}
            filterPlaceholder="Search..."
            {...tableData}
          >
          <DataTable
              columns={columns}
              data={data}
              print={false}
              pagination
              highlightOnHover
            /></DataTableExtensions>
            </CustomBox>
          </CustomBox>
        </CustomContainer>
      );
}