import React, { useContext, useState } from "react";
import axios from "axios";
import { useFormik } from "formik";
import * as yup from "yup";
import {
  UserContext,
  SnackContext,
  CategoryContext,
  UserProContext,
} from "../../context/context";

export const useEditTeam = () => {
  const { setUser } = useContext(UserContext);
  const [user1, setuser1] = useState({
    id: "",
    name: "",
    email: "",
    mobile: "",
    status: "",
  });
  const { setSnack } = useContext(SnackContext);
  const { userProfile } = useContext(UserProContext);
  const { category } = useContext(CategoryContext);
  const [open_edit, setOpen_edit] = useState(false);
  const getSingleUser = (id) => {
    const formdata = new FormData();
    console.log(id);
    formdata.append("userid", id);
    console.log("bhujhb");
    axios.post("/api/select_single_sub_cct", formdata).then(function (res) {
      if (res.data.status === true) {
        console.log(res.data);
        setuser1({
          id: res.data.data.user_id,
          name: res.data.data.user_sub_name,
          mobile: res.data.data.user_sub_mobile,
          email: res.data.data.user_sub_mail,
        });
      } else {
        setuser1({
          data: [],
          noData: false,
        });
      }
    });
  };
  const editClick = (data) => {
    console.log(data, "hi");
    getSingleUser(`${data}`);
    setOpen_edit(true);
  };
  const formikEdit = useFormik({
    initialValues: {
      id: user1.id,
      name: user1.name,
      email: user1.email,
      mobile: user1.mobile,
    },
    enableReinitialize: true,
    validationSchema: yup.object({
      name: yup
        .string()
        .min(2, "Too Short!")
        .max(50, "Too Long!")
        .required("Name is Required"),
      email: yup
        .string()
        .email("Enter a valid email")
        .required("Email is required"),
      mobile: yup
        .string()
        .min(10, "Enter a valid Mobilenumber of 10 numbers")
        .max(10, "Mobilenumber is not valid")
        .required("Mobilenumber is required"),
      // role: yup
      //     .string()
      //     .required("Role is required"),
    }),
    onSubmit: (values, { setSubmitting }) => {
      console.log(values);
      console.log("============================");
      const formdata = new FormData();
      formdata.append("userid", values.id);
      formdata.append("subname", values.name);
      formdata.append("submail", values.email);
      formdata.append("submobile", values.mobile);
      // formdata.append("user_cat_id", category.id);
      // formdata.append("user_cat", category.name);

      axios
        .post("/api/update_single_sub_cct", formdata)
        .then(function (res) {
          console.log(res);
          console.log("==================update====================");
          if (res?.data?.status === true) {
            setSnack({
              message: res.data.msg,
              type: "success",
              open: true,
            });
            setOpen_edit(false);
          } else {
            console.log("Error");

            setSnack({
              message: res.data.msg,
              type: "success",
              open: true,
            });
            setOpen_edit(false);
          }
          getdata();
        });
    },
  });
  const getdata = () => {
    const formdata = new FormData();
    formdata.append("usubcatid", category.id);
    formdata.append("cctid", userProfile.user_id);
    axios.post("/api/select_sub_cct", formdata).then(function (res) {
      if (res.data.status === true) {
        setUser(res.data.data);
      }
    });
  };
  return [editClick, formikEdit, open_edit, setOpen_edit];
};
